/* animation text-focus-in */
 @-webkit-keyframes text-focus-in {
    0% {
        -webkit-filter: blur(12px);
        filter: blur(12px);
        opacity: 0;
    }
    100% {
      -webkit-filter: blur(0px);
              filter: blur(0px);
      opacity: 1;
    }
  }
  @keyframes text-focus-in {
    0% {
      -webkit-filter: blur(12px);
              filter: blur(12px);
      opacity: 0;
    }
    100% {
      -webkit-filter: blur(0px);
              filter: blur(0px);
      opacity: 1;
    }
  }

/* animation slide-in-top */
@-webkit-keyframes slide-in-top {
    0% {
      -webkit-transform: translateY(-1000px);
              transform: translateY(-1000px);
      opacity: 0;
      -webkit-filter: blur(10px);
              filter: blur(10px);
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
      -webkit-filter: blur(0px);
              filter: blur(0px);
    }
  }
  @keyframes slide-in-top {
    0% {
      -webkit-transform: translateY(-1000px);
              transform: translateY(-1000px);
      opacity: 0;
      -webkit-filter: blur(10px);
              filter: blur(10px);
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
      -webkit-filter: blur(0px);
              filter: blur(0px);
    }
  }
/* animation slide-in-bottom */
@-webkit-keyframes slide-in-bottom {
    0% {
      -webkit-transform: translateY(70px);
              transform: translateY(70px);
      opacity: 0;
      -webkit-filter: blur(4px);
              filter: blur(4px);
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
      -webkit-filter: blur(0px);
              filter: blur(0px);
    }
  }
  @keyframes slide-in-bottom {
    0% {
      -webkit-transform: translateY(70px);
              transform: translateY(70px);
      opacity: 0;
      -webkit-filter: blur(4px);
              filter: blur(4px);
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
      -webkit-filter: blur(0px);
              filter: blur(0px);
    }
  }
  /* animation slide-in-bid */
@-webkit-keyframes slide-in-bid {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
    opacity: 0;
    -webkit-filter: blur(2px);
            filter: blur(2px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
    -webkit-filter: blur(0px);
            filter: blur(0px);
  }
}
@keyframes slide-in-bid {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
    opacity: 0;
    -webkit-filter: blur(2px);
            filter: blur(2px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
    -webkit-filter: blur(0px);
            filter: blur(0px);
  }
}


/*
 * animation pulsate-bck
 */
 @-webkit-keyframes pulsate-bck {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);

  }
}
@keyframes pulsate-bck {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    color: #000          

  }
  16% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  30% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  45% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  60% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  75% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    color: #00f035        
  }
}

@-webkit-keyframes blur-bck {
  0% {
    -webkit-filter: blur(0px);
    filter: blur(0px);
    color: #000          

  }
  20% {
    -webkit-filter: blur(15px);
    filter: blur(15px);
    color: #fff    
  }
  40% {
    -webkit-filter: blur(0px);
    filter: blur(0px);
    color: #00f035;   
  }
  60% {
    color: #00f035;   
  }
  100% {
    color: #000        
  }
}

@keyframes blur-bck {
  0% {
    -webkit-filter: blur(0px);
    filter: blur(0px);
    color: #000          

  }
  20% {
    -webkit-filter: blur(15px);
    filter: blur(15px);
    color: #fff    
  }
  40% {
    -webkit-filter: blur(0px);
    filter: blur(0px);
    color: #00f035;   
  }
  60% {
    color: #00f035;   
  }
  100% {
    color: #000        
  }
}





        

